    login () {
      'use strict'

      let _this = this
      let jqUsername = $('input.username')
      let jqPassword = $('input.password')
      let jqLogin = $('section.menu a.login')
      let jqRegister = $('section.menu a.register')

      let username = jqUsername.val().trim()
      let password = jqPassword.val().trim()
      let choice = jqLogin.text()

      if (choice === '') {
        if (confirm('')) {
          jqUsername.show()
          jqPassword.show()
          jqLogin.text('')
          // 
          if (jqRegister.hasClass('disabled')) {
            jqRegister.removeClass('disabled')
          }
          // 
          if (this.$store.state.loginFlag) {
            this.$store.dispatch('changeLoginFlag', false)
          }
        }
      } else if (choice === '') {
        if (!username || username === '') {
          console.log('')
          jqUsername.focus()
        } else if (!password || password === '') {
          console.log('')
          jqPassword.focus()
        } else {
          let serverUrl = 'login'
          let postData = {}
          postData.username = username
          postData.password = password

          // jqUsername.hide()
          // jqPassword.hide()
          // jqLogin.text('')
          // // 
          // if (!jqRegister.hasClass('disabled')) {
          //   jqRegister.addClass('disabled')
          // }
          // // 
          // if (!this.$store.state.loginFlag) {
          //   this.$store.dispatch('changeLoginFlag', true)
          // }
          //
          // // 
          // this.$router.replace('/login/' + expertId)

          // HTTP Request (JQuery Ajax)
          $.ajax({
            type: 'GET',
            url: serverUrl,
            data: postData,
            // crossDomain: true,
            // dataType: "jsonp",
            // jsonp: "jsonp_callback",
            // jsonpCallback: "jsonpCallbackFunction",
            dataType: 'json',
            // async: true, // 
            // timeout: 20000, // 20
            beforeSend: function (xhr) {
              // loading
              // $('div.load-container').css({'display': 'block'})
            },
            success: function (result, status, xhr) {
              console.log(result)
              if (!result || result === '' || result.length === 0) {
                alert('')
                console.log('')
                return false
              } else {
                // JSON
                let userInfo = JSON.parse(result.myuser)
                let favoriteExpertList = []

                let favoriteExpertListTemp = JSON.parse(result.favExperts)
                favoriteExpertListTemp.forEach(function (item, index) {
                  favoriteExpertList.push(JSON.parse(item))
                })

                let flag = true

                // 
                let testData = userInfo
                if (JSON.stringify(testData) !== '{}') {
                  if (testData !== undefined) {
                    if ('id' in testData) {
                      if (testData.id === undefined) {
                        flag = false
                      }
                    } else {
                      flag = false
                    }
                  } else {
                    flag = false
                  }
                } else {
                  flag = false
                }

                if (flag) {
                  console.log('')
                  jqUsername.hide()
                  jqPassword.hide()
                  jqLogin.text('')
                  // 
                  if (!jqRegister.hasClass('disabled')) {
                    jqRegister.addClass('disabled')
                  }
                  //  VUEX 
                  if (!_this.$store.state.loginFlag) {
                    _this.$store.dispatch('changeLoginFlag', true)
                  }

                  // 
                  let popStateData = {}
                  popStateData.userInfo = userInfo
                  popStateData.favoriteExpertList = favoriteExpertList
                  console.log(popStateData)

                  if (location.href.indexOf('#') !== -1) {
                    console.log(': ' + location.href.split('#')[0] + '#/user_center/' + userInfo.id)
                    history.pushState(popStateData, document.title, location.href.split('#')[0] +
                      '#/user_center' + userInfo.id)
                  } else {
                    console.log(': ' + location.href)
                    history.pushState(popStateData, document.title, location.href)
                  }

                  // 
                  _this.$router.push({
                    name: 'UserCenter', params: {userData: popStateData}
                  })
                } else {
                  alert('')
                  console.log('')
                  return false
                }
              }
            },
            error: function (xhr, status, error) {
              // loading
              $('div.load-container').css({'display': 'none'})
              alert('HTTP GET Error! status=' + status + '&error=' + error +
                '&statusCode=' + xhr.status + '&responseText=' + xhr.responseText +
                '&readyState=' + xhr.readyState)
            },
            statusCode: {
              // 
              200: function () {
                console.log('200: ')
              },
              404: function () {
                console.log('404: ')
                alert('404: ')
              },
              500: function () {
                console.log('500: ')
                alert('500: ')
              }
            }
          })
        }
      } else {
        console.error('choice error !!!')
      }
    },
