    //  CCN 
    ccnToList (cnnNumber) {
      let result
      if (cnnNumber === 'A' || cnnNumber === 'a' || cnnNumber === 'ccn-a') {
        result = 0
      } else if (cnnNumber === 'B' || cnnNumber === 'b' || cnnNumber === 'ccn-b') {
        result = 1
      } else if (cnnNumber === 'C' || cnnNumber === 'c' || cnnNumber === 'ccn-c') {
        result = 2
      } else if (cnnNumber === 'D' || cnnNumber === 'd' || cnnNumber === 'ccn-d') {
        result = 3
      } else if (cnnNumber === 'E' || cnnNumber === 'e' || cnnNumber === 'ccn-e') {
        result = 4
      } else if (cnnNumber === 'F' || cnnNumber === 'f' || cnnNumber === 'ccn-f') {
        result = 5
      } else if (cnnNumber === 'G' || cnnNumber === 'g' || cnnNumber === 'ccn-g') {
        result = 6
      } else if (cnnNumber === 'H' || cnnNumber === 'h' || cnnNumber === 'ccn-h') {
        result = 7
      } else if (cnnNumber === 'I' || cnnNumber === 'i' || cnnNumber === 'ccn-i') {
        result = 8
      } else if (cnnNumber === 'J' || cnnNumber === 'j' || cnnNumber === 'ccn-j') {
        result = 9
      } else if (cnnNumber === 'K' || cnnNumber === 'k' || cnnNumber === 'ccn-k') {
        result = 10
      } else if (cnnNumber === 'N' || cnnNumber === 'n' || cnnNumber === 'ccn-n') {
        result = 11
      } else if (cnnNumber === 'O' || cnnNumber === 'o' || cnnNumber === 'ccn-o') {
        result = 12
      } else if (cnnNumber === 'P' || cnnNumber === 'p' || cnnNumber === 'ccn-p') {
        result = 13
      } else if (cnnNumber === 'Q' || cnnNumber === 'q' || cnnNumber === 'ccn-q') {
        result = 14
      } else if (cnnNumber === 'R' || cnnNumber === 'r' || cnnNumber === 'ccn-r') {
        result = 15
      } else if (cnnNumber === 'S' || cnnNumber === 's' || cnnNumber === 'ccn-s') {
        result = 16
      } else if (cnnNumber === 'T' || cnnNumber === 't' || cnnNumber === 'ccn-t') {
        result = 17
      } else if (cnnNumber === 'U' || cnnNumber === 'u' || cnnNumber === 'ccn-u') {
        result = 18
      } else if (cnnNumber === 'V' || cnnNumber === 'v' || cnnNumber === 'ccn-v') {
        result = 19
      } else if (cnnNumber === 'X' || cnnNumber === 'x' || cnnNumber === 'ccn-x') {
        result = 20
      } else if (cnnNumber === 'Z' || cnnNumber === 'z' || cnnNumber === 'ccn-z') {
        result = 21
      } else {
        result = 0
      }
      return result
    },
    //  div 
    listToDiv (listNumber) {
      let result
      if (listNumber === 0 || listNumber === 1 || listNumber === 2 || listNumber === 3 || listNumber === 4) {
        result = 1
      } else if (listNumber === 5 || listNumber === 6 || listNumber === 7 || listNumber === 8 || listNumber === 9) {
        result = 2
      } else if (listNumber === 10 || listNumber === 11 || listNumber === 12 ||
        listNumber === 13 || listNumber === 14) {
        result = 3
      } else if (listNumber === 15 || listNumber === 16 || listNumber === 17) {
        result = 4
      } else if (listNumber === 18 || listNumber === 19 || listNumber === 20 || listNumber === 21) {
        result = 5
      } else {
        result = 1
      }
      return result
    },
    // AJAX 
    queryListAjax (postData, currentPage) {
      'use strict'
      let _this = this
      let serverUrl = 'expert_list'

      if (postData.query_type !== 'normal' && postData.query_type !== 'advanced') {
        // loading
        $('div.load-container').css({'display': 'none'})
        return false
      }
      postData.current_page = currentPage

      // HTTP GET (JQuery Ajax)
      $.ajax({
        type: 'GET',
        url: serverUrl,
        data: postData,
        // crossDomain: true,
        // dataType: "jsonp",
        // jsonp: "jsonp_callback",
        // jsonpCallback: "jsonpCallbackFunction",
        dataType: 'json',
        // async: true, // 
        // timeout: 20000, // 20
        beforeSend: function (xhr) {
          // loading
          $('div.load-container').css({'display': 'block'})
        },
        success: function (result, status, xhr) {
          // alert('HTTP GET Success! normal_query: ' + 'result=' + result);
          // 
          _this.expertQueryList = []
          _this.expertQueryListPageTotal = 0
          _this.expertQueryListPageCurrent = currentPage

          // 
          let itemPerPage = 10

          if (!result || result === '' || result.length === 0) {
            // 
            let htmlTemplate = '<p id="search-result-tag"></p>'
            //  query-result-list 
            //  query-result-list 
            $('.query-result-list').empty().append(htmlTemplate)
            return false
          }

          _this.expertQueryList = JSON.parse('[' + result + ']')

          let totalPage = _this.expertQueryList[_this.expertQueryList.length - 1]
          _this.expertQueryListPageTotal = Math.floor(totalPage / itemPerPage) + 1

          result = _this.expertQueryList

          console.log(result)

          //  AJAX 
          _this.getListAjaxData(_this)

          // 
          _this.renderQueryList()
        },
        error: function (xhr, status, error) {
          // loading
          $('div.load-container').css({'display': 'none'})
          alert('HTTP GET Error! status=' + status + '&error=' + error +
            '&statusCode=' + xhr.status + '&responseText=' + xhr.responseText +
            '&readyState=' + xhr.readyState)
        },
        statusCode: {
          // 
          200: function () {
            console.log('200: ')
          },
          404: function () {
            console.log('404: ')
            alert('404: ')
          },
          500: function () {
            console.log('500: ')
            alert('500: ')
          }
        }
      })

      // 
      // this.$router.push('/index/' + postData.queryUrl)
    },
    // 
    renderQueryList () {
      let _this = this
      let result = _this.expertQueryList

      // loading
      $('div.load-container').css({'display': 'none'})

      // 
      let itemPerPage = 10

      let jqCurrentPage = $('#query-result-current-page')
      let jqTotalPages = $('#query-result-total-pages')
